/**
 * API utility functions for authenticated requests
 * Uses JWT token from localStorage for authorization
 */

/**
 * Get authorization headers with JWT token
 * @returns {Object} Headers object with Authorization if token exists
 */
export function getAuthHeaders() {
  const token = localStorage.getItem("token");
  const headers = {
    Accept: "application/json",
    "Content-Type": "application/json",
  };
  if (token) {
    headers.Authorization = `Bearer ${token}`;
  }
  return headers;
}

/**
 * Make an authenticated fetch request
 * Automatically includes JWT token in Authorization header
 * @param {string} url - The URL to fetch
 * @param {Object} options - Fetch options
 * @returns {Promise<Response>}
 */
export async function authFetch(url, options = {}) {
  const token = localStorage.getItem("token");
  
  const headers = {
    Accept: "application/json",
    ...options.headers,
  };
  
  if (token) {
    headers.Authorization = `Bearer ${token}`;
  }
  
  // Keep credentials for backward compatibility with session-based auth
  const response = await fetch(url, {
    ...options,
    headers,
    credentials: "include",
  });
  
  // Handle 401 unauthorized - clear token and redirect to login
  if (response.status === 401) {
    localStorage.removeItem("token");
    localStorage.removeItem("user");
    // Don't redirect automatically - let the component handle it
  }
  
  return response;
}

/**
 * Make an authenticated GET request
 * @param {string} url - The URL to fetch
 * @returns {Promise<Response>}
 */
export function authGet(url) {
  return authFetch(url, { method: "GET" });
}

/**
 * Make an authenticated POST request
 * @param {string} url - The URL to fetch
 * @param {Object} body - The request body
 * @returns {Promise<Response>}
 */
export function authPost(url, body) {
  return authFetch(url, {
    method: "POST",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(body),
  });
}

/**
 * Make an authenticated PUT request
 * @param {string} url - The URL to fetch
 * @param {Object} body - The request body
 * @returns {Promise<Response>}
 */
export function authPut(url, body) {
  return authFetch(url, {
    method: "PUT",
    headers: { "Content-Type": "application/json" },
    body: JSON.stringify(body),
  });
}

/**
 * Make an authenticated DELETE request
 * @param {string} url - The URL to fetch
 * @returns {Promise<Response>}
 */
export function authDelete(url) {
  return authFetch(url, { method: "DELETE" });
}

/**
 * Check if user is authenticated (has token)
 * @returns {boolean}
 */
export function isAuthenticated() {
  return !!localStorage.getItem("token");
}

/**
 * Get current user from localStorage
 * @returns {Object|null}
 */
export function getCurrentUser() {
  const userStr = localStorage.getItem("user");
  if (userStr) {
    try {
      return JSON.parse(userStr);
    } catch {
      return null;
    }
  }
  return null;
}

/**
 * Clear authentication data
 */
export function clearAuth() {
  localStorage.removeItem("token");
  localStorage.removeItem("user");
}
